.input-el {
  background: none;
  border-style: none;
  padding: 0;
  outline: none;
  position: absolute;
  opacity: 0;
}

.input-el.dateinput {
  width: 2ch;
}

.input-el::selection {
  background: none;
}

.text-display {
  font-weight: bold;
}

.text-display.suggestion {
  opacity: 0.75;
}

.wrapper {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  margin: 0 0.5ch 0 0.5ch;
  transition: background-color, left, right, 0.3s;
}

.wrapper.dow {
  position: relative;
}

.wrapper.dateinput {
  width: 2.75ch;
  height: 2.75ch;
  position: absolute;
}

.wrapper.month {
  z-index: 1;
  left: 0;
}

.wrapper.day {
  z-index: 2;
  left: 0;
}

.wrapper.year {
  z-index: 1;
  right: 0;
}

.wrapper.day.form-selected {
  left: 33%;
}

.hidden {
  visibility: hidden;
  transition: 0s;
}

.dow {
  width: 5ch;
  height: 2.75ch;
}